body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fffcf9;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin:0;
    padding:0;
}

.home{
    display: flex;
    flex-direction: column;
    height: calc(100vh-1em);
    width: 100vw;
}

.home .title-flex{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f2af29;
}

.home .title-flex h1{
    margin-top: 5%;
    font-size: 6em;
    color: #05a8aa;
}

.home .title-flex p{
    margin-bottom: 6%;
    font-size: 3em;
    color: #fffcf9;
}

.home .wrapper{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
    line-height: 1.6;
}

.home .wrapper h2{
    font-size: 2em;
    color: #05a8aa;
}

.home .wrapper p{
    font-size: 1.25em;
    color: #474747;
}

.home .wrapper a{
    font-size: 1.25em;
    color: #db5461;
}

.home .wrapper .about-me{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 44%;
    margin: 3%;
}

.home .wrapper .about-me button{
    margin-top: 1%;
    border-color: #DB5461;
    padding: .5%;
    font-size:1.25em;
    color: #DB5461;
    background-color: #dbcfb000;
    align-self: flex-start;
    text-decoration: none;
    transition: border-color 1.5s, color 1.5s;
}

.home .wrapper .about-me button:hover{
    color: #05a8aa;
    border-color: #f2af29;
}

.home .wrapper .current{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 44%;
    margin: 3%;
}

@media (max-width: 800px){
    .home .title-flex h1{
        margin-top: 6%;
        font-size: 3.2em;
    }

    .home .title-flex p{
        margin-bottom: 7%;
        font-size: 1em;
    }

    .home .wrapper{
        flex-direction: column;
    }

    .home .wrapper .about-me{
        width: 94%;
    }

    .home .wrapper .current{

        align-items: flex-start;
        width: 94%;
    }

    .home .wrapper h2{
        align-self: center;
        text-align: center;
    }
}
.link{
    height: 1em;
    color: #474747;
    display: inline-block;
    margin: 0 .5em;
    transition: color .5s;
}

.active{
    border-bottom: solid;
    border-width:1px;
    border-color: #db5461;
    padding-bottom: .3em;
}

.link:hover{
    color: #05a8aa;
}

.nav-links{
    padding: .5em 1em;
    display: flex;
    justify-content: flex-end;
}

@media (max-width: 800px){
    .nav-links{
        justify-content: center;
    }
}
.about-page{
    display: flex;
    flex-direction: column;
    height: calc(100vh-1em);
    width: 100vw;
}

.about-page h1{
    font-size: 6em;
    align-self: center;
    color: #f2af29;
}

.about-page .text-wrapper{
    margin: 3%;
    display: flex;
    flex-direction: column;
    line-height: 1.6;
}

.about-page .text-wrapper h2{
    font-size: 2em;
    color: #05a8aa;
}

.about-page .text-wrapper p{
    font-size: 1.25em;
    color: #474747;
}

.about-page .text-wrapper a{
    font-size: 1.25em;
    color: #db5461;
    text-decoration: none;
}

.about-page .bottom-wrapper{
    display:flex;
    width: 100%;
    flex-direction: row;
}

.about-page .bottom-wrapper img{
    align-self: flex-end;
    max-width: 30%;
    height: auto;
    margin-left: 3%;
    margin-right: 3%;
    border-radius: .11em;
}

.about-page .bottom-wrapper .info{
    display: flex;
    flex-direction: column;
}

.about-page .bottom-wrapper .info .smaller-info{
    display: flex;
    flex-direction: row;
}

.about-page .bottom-wrapper .info .smaller-info .end-wrapper{
    align-self: flex-end;
}

@media (max-width: 800px){
    .about-page h1{
        font-size: 5em;
    }

    .about-page .text-wrapper h2{
        text-align: center;
    }

    .about-page .bottom-wrapper img{
        display: none;
    }

    .about-page .bottom-wrapper .info .smaller-info{
        flex-direction: column;
    }

    .about-page .bottom-wrapper .info .smaller-info .end-wrapper{
        align-self: center;
        text-align: center;
    }

    .about-page .text-wrapper{
        line-height: 1;
    }
}
.projects-page{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.projects-page .title-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: solid;
    border-width: .01em;
    border-color: #474747;
}

.projects-page .title-wrapper h1{
    color: #f2af29;
    font-size: 6em;
    margin-bottom: 1%;
}

.projects-page .project-list{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

@media (max-width: 800px){
    .projects-page .title-wrapper h1{
        font-size: 5em;
    }

    .projects-page .project-list{
        align-items: center;
        flex-direction: column;
    }
}
.singleProj-container{
    display: flex;
    background-color:#e8e6e3;
    flex-direction: column;
    margin: 2% 2%;
    width: 15%;
    height: 90%;
    align-items: center;
}

.singleProj-container a{
    color: #474747;
}

.singleProj-container img{
    margin-top: 2%;
    height: 250px;
    width: auto;
    max-width: 90%;
    max-height: 90%;
}

.singleProj-container .projInfo{
    margin: 5%;
    height: 10%;
}

@media (max-width: 800px){
    .singleProj-container{
        width: 90%;
    }
}
.reflections-page{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.reflections-page .title-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: solid;
    border-width: .01em;
    border-color: #474747;
}

.reflections-page .title-wrapper h1{
    color: #f2af29;
    font-size: 6em;
    margin-bottom: 1%;
}

.reflection-page .reflections-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media (max-width: 800px){
    .reflections-page .title-wrapper h1{
        font-size: 4em;
    }
}
.singleReflection-container{
    margin: 2% 20% 2% 20%;
}

.singleReflection-container h2{
    font-size: 2em;
    color: #05a8aa;
}

.singleReflection-container p{
    font-size: 1.15em;
    color: #474747;
}

@media (max-width: 800px){
    .singleReflection-container{
        margin-bottom: 5%;
        margin-top:5%;
    }
    .singleReflection-container p{
        font-size: 1.05em;
    }
}
